<template>
  <div>
    <v-container
      fluid
      grid-list-md
    >
      <v-layout row wrap>
        <v-flex xs12 sm6 md4 lg3>
          <v-card color="#26c6da" class="white--text" to="/roomState" v-ripple>
            <v-card-title class="grow">
              <div>
                <div class="title">当前已租 {{ roomNum.rented }} 间</div>
                <div class="subheading">总 数： {{ roomNum.total }} 间</div>
                <div class="subheading">空 房： {{ roomNum.vacant }} 间</div>
                <div class="subheading">脏 房： {{ roomNum.dirty }} 间</div>
                <div class="subheading">维 修： {{ roomNum.repair }} 间</div>
                <div class="subheading">入住率： {{ roomNum.occupancy }}</div>
              </div>
              
            </v-card-title>
          </v-card>
        </v-flex>
        <v-flex xs12 sm6 md4 lg3>
          <v-card color="#26c6da" class="white--text">
            <v-card-title>
              <div>
                <div class="title">当前现金 {{ onDutyInfo ? onDutyInfo.currentCash : '0' }} 元</div>
                <div class="subheading">当  班 人 ：{{ onDutyInfo ? onDutyInfo.onComingRealName : '无' }}</div>
                <div class="subheading">接班时间：{{ onDutyInfo ? onDutyInfo.onComingTime : '无' }}</div>
                <div class="subheading">接班现金：+{{ onDutyInfo ? onDutyInfo.lastHandoverCash : '0' }} 元</div>
                <div v-for="(item, index) in cashFlowInfo" :key="index" class="subheading">
                  {{ item.item  }}： 
                  <!-- <span class="redFont"> -->
                  {{ item.money > 0 ? '+'+item.money : item.money }} 
                  <!-- </span> -->
                    元
                </div>
              </div>
            </v-card-title>
          </v-card>
        </v-flex>
        <v-flex xs12 sm6 md4 lg3>
          <v-card color="#26c6da" class="white--text">
            <v-card-title class="grow">
              <div>
                <div class="title">当前班次订单结余： {{ onDutyInfo &&onDutyInfo.currentOrderBalance ? onDutyInfo.currentOrderBalance : 0 }} 元</div>
                <div v-for="(item, index) in onDutyInfo && onDutyInfo.orderInOut ? onDutyInfo.orderInOut : []" :key="index" class="subheading">
                  {{ getText('pay_method', item.payMethodCode)  }}：收入:{{ item.income }} 
                  支出:{{ item.outgoings}} 
                  &nbsp;结余:{{item.balance}} 
                </div>
              </div>
              
            </v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
import { getOnDuty, handover } from "@/api/handover.js";
export default {
  name: 'Overview',
  data() {
    return {
      onDutyInfo: null
    }
  },
  computed: {
    cashFlowInfo() {
      let collectCash = 0;
      let otherCashIncome = 0;
      let otherCashOutgoings =0;
      let orderBalance = 0;
    //   let orderCash
      if (this.onDutyInfo && this.onDutyInfo.otherFrontCashFlows) {
          this.onDutyInfo.otherFrontCashFlows.forEach(flow => {
                if (flow.itemCode === 'collect_cash') {
                    collectCash += flow.flowMoney;
                } else {
                    if (flow.flowType === 'income') {
                        otherCashIncome += flow.flowMoney;
                    } else {
                        otherCashOutgoings += flow.flowMoney;
                    }
                }
        });
    }
    if (this.onDutyInfo && this.onDutyInfo.orderInOut) {
        let inout = this.onDutyInfo.orderInOut.filter(inout => inout.payMethodCode === 'cash');
        inout[0] ? orderBalance = inout[0].balance : orderBalance = 0;
    }
    let info = [
        {"money" : orderBalance, "item":"订单结余"},
        {"money" : -collectCash, "item":"财务收账"},
        {"money" : otherCashIncome, "item":"其它收入"},
        {"money" : otherCashOutgoings, "item":"其它支出"}
        ];
      return info;
      },
    ...mapGetters([
      'roomNum',
      'getText'
    ])
  },
  mounted() {
    this.rooms && this.rooms.length <=0 ?'':this.getRooms();
    this.getOnDutyInfo();
  },
  methods: {
    getOnDutyInfo () {
        getOnDuty().then(res => {
            if (res.data.code === 0) {
                this.onDutyInfo = res.data.data;
            } else {
                this.$Msg.error(res.data.code + "：" + res.data.msg);
            }
        });
    },
    ...mapActions({
      getRooms: "getRooms",
      getDicByType: "getDicByType"
    })
  },
}
</script>

